import React from "react";
import PropTypes from "prop-types";
import {Graphic} from "./Utils"
import {Price} from "./PriceRow";
import DeviceType from '../../../enum/DeviceType';
import RelationType from '../../../enum/RelationType';

const FullProductRow = (props) => {
    const oneTimeCost = props.product.price && props.product.price.oneTimeCost;
    const hasNonZeroOneTimeCost = oneTimeCost && oneTimeCost.price > 0;
    const monthlyCost = props.product.price && props.product.price.monthlyCosts
        && props.product.price.monthlyCosts.length > 0 && props.product.price.monthlyCosts[0];
    const tieredPrices = props.product.price && props.product.price.monthlyCosts
        && props.product.price.monthlyCosts.length > 1 && props.product.price.monthlyCosts.slice(1);
    const hasZeroMonthlyCost = monthlyCost && monthlyCost.price <= 0;
    const hasMoreThanOneMonthlyCost= props.product.price && props.product.price.monthlyCosts
        && props.product.price.monthlyCosts.length > 1;
    const isDiscount = props.product.price < 0  || monthlyCost && monthlyCost.price < 0;
    const isFree = hasZeroMonthlyCost && !hasNonZeroOneTimeCost && !hasMoreThanOneMonthlyCost;
    const showFreeLabel = isFree && props.forFreeLabel && !isDiscount;
    const showOptionalSTBLabel = DeviceType.STB === props.product.deviceType && RelationType.OPTIONAL === props.product.relationType;
    const showOneTimeCostAsMain = hasNonZeroOneTimeCost && (!monthlyCost || hasZeroMonthlyCost);
    const installments = props.product.price && props.product.price.installments;
    const productIsDeviceType= !!props.product.deviceType;
    const productIsFreeLabel= productIsDeviceType ? props.forFreeLabel : props.serviceFreeLabel;
    return (
        <div className={`l-row ${props.borderTop ? 'u-border-top' : ''}`}>
            {/*Note: first column is on the right due to 'l-col--opposite' class*/}
            <div className="l-col l-col-4 l-col-small-12 l-col--opposite">
                <div className={`l-row ${showFreeLabel || !!tieredPrices ? 'u-padding-l u-padding-top-l' : 'u-padding-l-xl u-padding-top-l-xl'} u-small-no-padding`}>
                    <div className={`l-col l-col-6 l-col-small-12 u-padding-right u-text-right ${showOneTimeCostAsMain ? 'u-small-text-left u-small-padding-s u-small-padding-top-m' : 'u-small-hide'}`}>
                        {
                            hasNonZeroOneTimeCost &&
                            <Price price={oneTimeCost} net={props.net}/>
                        }
                    </div>
                    <div className={`l-col l-col-6 l-col-small-12 u-padding-right u-text-right ${!showOneTimeCostAsMain ? 'u-small-text-left u-small-padding-s u-small-padding-top-m' : 'u-small-hide'}`}>
                        {
                            monthlyCost &&
                            <Price price={{...monthlyCost, installments}} net={props.net}/>
                        }
                        {
                            showFreeLabel &&
                            <div>
                                <span dangerouslySetInnerHTML={{__html: productIsFreeLabel}}/>
                            </div>
                        }
                        {
                            showOptionalSTBLabel &&
                            <div>
                                <span dangerouslySetInnerHTML={{__html: props.optionalDeviceLabel}}/>
                            </div>
                        }
                        {
                            tieredPrices &&
                            tieredPrices.map(tieredPrice => {
                                return <div key={tieredPrice.monthFrom + "-" + tieredPrice.monthTo}
                                            className="u-right u-text-nowrap">
                                    od&nbsp;{tieredPrice.monthFrom} {tieredPrice.monthTo ? "do " + tieredPrice.monthTo + " " : ""}m-ca {props.net ? tieredPrice.net : tieredPrice.gross} {tieredPrice.currency}
                                </div>
                            })
                        }
                        {
                            !showOneTimeCostAsMain && hasNonZeroOneTimeCost &&
                            <div className="u-font-small u-font-bold u-large-hide u-medium-hide">
                                +&nbsp;{props.net ? oneTimeCost.net : oneTimeCost.gross} {oneTimeCost.currency}&nbsp;jednorazowa opłata
                            </div>
                        }
                        {
                            monthlyCost && !!props.lowerInstallmentsClicked && !(!hasMoreThanOneMonthlyCost && hasZeroMonthlyCost) &&
                            <div>
                                <a className="u-spacing-left" href="#" key='lowerInstallmentsClicked_key'
                                   onClick={(props.lowerInstallmentsClicked)}
                                   aria-label="Obniż raty">{!!props.descriptions && props.descriptions.lowerInstallments || "Obniż raty"}
                                </a>
                            </div>
                        }
                    </div>
                </div>
            </div>
            <div className="l-col l-col-8 l-col-small-12">
                <div className={`l-row ${showFreeLabel ? 'u-padding-l-xl u-padding-top-l-xl' : 'u-padding-l u-padding-top-l'} u-small-padding-top u-small-padding-l`}>
                    <div className={`l-col l-col--auto 'u-no-padding-r'`}>
                        <div className="opl-checkout__icon__cell">
                            {
                                (!!props.product.thumbnailIcon || !!props.product.imageUrl) &&
                                <Graphic icon={props.product.thumbnailIcon}
                                         imgUrl={props.product.imageUrl}/>
                            }
                        </div>
                    </div>
                    <div className={`l-col l-col--auto l-col-small-8 l-col-medium-8  'u-no-padding-l'`}>
                        <span className="u-font-bold u-small-font-large" dangerouslySetInnerHTML={{__html: props.product.name}}/>
                        <div className="u-padding-top-xs u-small-padding-top-s" dangerouslySetInnerHTML={{__html: props.product.description}}/>
                        <div className={`u-padding-top ${props.showActionsMobile ? '' : 'u-small-hide'}`}>
                            <div className={"l-row"}>
                                {
                                    props.onDetails &&
                                    <div className={"l-col l-col--auto "}>
                                        <div className={"u-padding-top"}>
                                            <a href="#" className="u-spacing-right-l u-text-nowrap"
                                               onClick={props.onDetails}>
                                                {props.detailsLabel}
                                            </a></div>
                                    </div>
                                }
                                {
                                    props.product.changeable && props.onChange &&
                                    <div className={"l-col l-col--auto"}>
                                        <div className={"u-padding-top"}>
                                            <a href="#" className="u-spacing-right-l u-text-nowrap "
                                               onClick={props.onChange}>
                                                {props.changeLabel}
                                            </a></div>
                                    </div>
                                }
                                {
                                    props.product.removable && props.onRemove &&
                                    <div className={"l-col l-col--auto  "}>
                                        <div className={"u-padding-top"}>
                                            <a href="#" className="u-spacing-right-l u-text-nowrap "
                                               onClick={props.onRemove}>
                                                {props.removeLabel}
                                            </a></div>
                                    </div>
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

FullProductRow.propTypes = {
    product: PropTypes.shape({
        price: PropTypes.shape({
            oneTimeCost: PropTypes.shape({
                gross: PropTypes.string,
                net: PropTypes.string,
                currency: PropTypes.string
            }),
            monthlyCosts: PropTypes.arrayOf(
                PropTypes.shape({
                    gross: PropTypes.string,
                    net: PropTypes.string,
                    currency: PropTypes.string,
                    monthFrom: PropTypes.number,
                    monthTo: PropTypes.number
                })
            ),
            installments: PropTypes.bool,
        }),
        code: PropTypes.string,
        name: PropTypes.string,
        thumbnailIcon: PropTypes.string,
        imageUrl: PropTypes.string,
        description: PropTypes.string,
        changeable: PropTypes.bool,
        removable: PropTypes.bool,
    }),
    detailsLabel: PropTypes.string,
    forFreeLabel: PropTypes.string,
    changeLabel: PropTypes.string,
    removeLabel: PropTypes.string,
    onDetails: PropTypes.func,
    onChange: PropTypes.func,
    lowerInstallmentsClicked: PropTypes.func,
    onRemove: PropTypes.func,
    borderTop: PropTypes.bool,
    showActionsMobile: PropTypes.bool,
    serviceFreeLabel: PropTypes.string,
    optionalDeviceLabel: PropTypes.string,
}

FullProductRow.defaultProps = {
    changeLabel: "Zmień",
    detailsLabel: "Szczegóły",
    removeLabel: "Usuń",
    borderTop: true,
    showActionsMobile: true,
    forFreeLabel: "<div>Urządzenie bezpłatne</div><div>w ramach oferty</div>",
    optionalDeviceLabel: "<div>Dekoder opcjonalny</div>",
    serviceFreeLabel: "<div>Bezpłatne w cenie</div><div>pakietu</div>"
}

export default FullProductRow;